
@charset "UTF-8";
#resumeEditor {
  background: #e7e8ea;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  overflow: auto;
}
#resumeEditor #logo {
    height: 64px;
    margin-top: 0;
    margin-bottom: 0;
}
#resumeEditor > nav {
    width: 80px;
    background: black;
    color: white;
    top: 0;
    height: 100vh;
    position: absolute;
}
#resumeEditor > nav > ol > li {
      height: 48px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      margin-top: 15px;
      margin-bottom: 20px;
}
#resumeEditor > nav > ol > li.active {
        background: white;
        color: black;
}
#resumeEditor > .panels {
    position: relative;
    /*.arrayResume {*/
    /*border-bottom: 1px solid black;*/
    /*border*/
    /*}*/
    background: #fff;
    min-width: 270px;
    width: 450px;
    margin-left: 110px;
    margin-right: 20px;
    margin-top: 15px;
    /*border:1px solid black;*/
    -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.25);
            box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.25);
    /*border:1px solid black;*/
    overflow: auto;
}
#resumeEditor > .panels .arrayResume + .arrayResume {
      margin-top: 120px;
}
#resumeEditor > .panels .arrayCt {
      position: relative;
}
#resumeEditor > .panels .bigButton {
      position: absolute;
      width: 100px;
      height: 40px;
      color: #ffffff;
      border: none;
      border-radius: 4px;
      opacity: .7;
      cursor: pointer;
}
#resumeEditor > .panels .bigButton:hover {
      opacity: 1;
}
#resumeEditor > .panels .btRemove {
      right: 5%;
      background-color: #c0392b;
}
#resumeEditor > .panels .btAdd {
      left: 5%;
      background-color: #02af5f;
}
#resumeEditor > .panels .Rborder {
      border-bottom: 4px solid rgba(138, 121, 210, 0.46);
      width: 90%;
      margin: 10px auto;
}
#resumeEditor > .panels .fieldName {
      width: 90%;
      margin: 20px 4% 20px 6%;
}
#resumeEditor > .panels > li {
      /*padding: 24px;*/
}
#resumeEditor > .panels > li h2 {
        margin-bottom: 24px;
}
#resumeEditor svg.icon {
    width: 28px;
    height: 28px;
}
#resumeEditor #skills {
    width: 40px !important;
    height: 40px !important;
}
#resumeEditor ol {
    list-style: none;
}
