    /* ANIMATIONS ================ */
}
#resumeEditor .resumeField > label {
      display: block;
}
#resumeEditor .resumeField h4, #resumeEditor .resumeField h5 {
      width: 90%;
      margin: 0 auto;
}
#resumeEditor .resumeField .group {
      margin-top: 45px;
      position: relative;
      margin-bottom: 45px;
      margin-left: auto;
      margin-right: auto;
      min-width: 250px;
      /* LABEL ======================================= */
      /* active state */
      /* BOTTOM BARS ================================= */
}
#resumeEditor .resumeField .group input {
        font-size: 16px;
        padding: 10px 10px 10px 5px;
        display: block;
        border: none;
        border-bottom: 1px solid #757575;
        margin: 0 auto;
        width: 90%;
        height: 35px;
}
#resumeEditor .resumeField .group input:focus {
        outline: none;
}
#resumeEditor .resumeField .group label {
        margin-left: 5%;
        color: #999;
        font-size: 16px;
        font-weight: normal;
        position: absolute;
        pointer-events: none;
        left: 5px;
        top: 6px;
        transition: 0.2s ease all;
        -moz-transition: 0.2s ease all;
        -webkit-transition: 0.2s ease all;
}
#resumeEditor .resumeField .group .countbutton {
        position: absolute;
        right: 5%;
        top: 20%;
        cursor: pointer;
}
#resumeEditor .resumeField .group #delete {
        color: #c0392b;
        opacity: .7;
}
#resumeEditor .resumeField .group #delete:hover {
        opacity: 1;
}
#resumeEditor .resumeField .group #add {
        color: #02af5f;
        width: 28px;
        height: 28px;
        opacity: .7;
}
#resumeEditor .resumeField .group #add:hover {
        opacity: 1;
}
#resumeEditor .resumeField .group input:focus ~ label, #resumeEditor .resumeField .group input:valid ~ label {
        top: -20px;
        font-size: 14px;
        color: #5264AE;
}
#resumeEditor .resumeField .group .bar {
        position: relative;
        display: block;
        width: 90%;
        margin: 0 auto;
}
#resumeEditor .resumeField .group .bar:before, #resumeEditor .resumeField .group .bar:after {
        content: '';
        height: 2px;
        width: 0;
        bottom: 1px;
        position: absolute;
        background: #5264AE;
        transition: 0.2s ease all;
        -moz-transition: 0.2s ease all;
        -webkit-transition: 0.2s ease all;
}
#resumeEditor .resumeField .group .bar:before {
        left: 50%;
}
#resumeEditor .resumeField .group .bar:after {
        right: 50%;
}
#resumeEditor .resumeField .array {
      margin-top: 45px;
      position: relative;
      margin-bottom: 45px;
      margin-left: auto;
      margin-right: auto;
      min-width: 250px;
      /* LABEL ======================================= */
      /* active state */
      /* BOTTOM BARS ================================= */
}
#resumeEditor .resumeField .array input {
        font-size: 16px;
        padding: 10px 10px 10px 5px;
        display: block;
        border: none;
        border-bottom: 1px solid #757575;
        margin: 0 15% 0 5%;
        width: 80%;
        height: 35px;
}
#resumeEditor .resumeField .array input:focus {
        outline: none;
}
#resumeEditor .resumeField .array label {
        margin-left: 5%;
        color: #999;
        font-size: 16px;
        font-weight: normal;
        position: absolute;
        pointer-events: none;
        left: 5px;
        top: 6px;
        transition: 0.2s ease all;
        -moz-transition: 0.2s ease all;
        -webkit-transition: 0.2s ease all;
}
#resumeEditor .resumeField .array input:focus ~ label, #resumeEditor .resumeField .array input:valid ~ label {
        top: -20px;
        font-size: 14px;
        color: #5264AE;
}
#resumeEditor .resumeField .array .bar {
        position: relative;
        display: block;
        width: 80%;
        margin-left: 5%;
}
#resumeEditor .resumeField .array .bar:before, #resumeEditor .resumeField .array .bar:after {
        content: '';
        height: 2px;
        width: 0;
        bottom: 1px;
        position: absolute;
        background: #5264AE;
        transition: 0.2s ease all;
        -moz-transition: 0.2s ease all;
        -webkit-transition: 0.2s ease all;
}
#resumeEditor .resumeField .array .bar:before {
        left: 50%;
}
#resumeEditor .resumeField .array .bar:after {
        right: 50%;
}
#resumeEditor .resumeField input:focus ~ .bar:before, #resumeEditor .resumeField input:focus ~ .bar:after {
      width: 50%;
}
#resumeEditor .resumeField .highlight {
      position: absolute;
      height: 60%;
      width: 100px;
      top: 25%;
      left: 5%;
      pointer-events: none;
      opacity: 0.5;
}
#resumeEditor .resumeField input:focus ~ .highlight {
      -webkit-animation: inputHighlighter 0.3s ease;
      animation: inputHighlighter 0.3s ease;
}
@-webkit-keyframes inputHighlighter {
from {
    background: #5264AE;
}
to {
    width: 0;
    background: transparent;
}
}
@keyframes inputHighlighter {
from {
    background: #5264AE;
}
to {
    width: 0;
    background: transparent;
}
}
#resumeEditor hr {
    border: none;
    border-top: 1px solid #ddd;
    margin: 24px 0;
}
#resumeEditor .subitem {
    position: relative;
}
#resumeEditor .subitem .button.remove {
      position: absolute;
      right: 0;
      top: -3px;
}



