
@charset "UTF-8";
html {
  background: #eaebec;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  /*使用SCSS语法嵌套，设置字体*/
}
.page {
  /*高度设置为浏览器可视化窗口高度，布局方式为flex，方向是垂直*/
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #EAEBEC;
}
.page > main {
    /*设置响应式*/
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    min-width: 1024px;
    margin-top: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /*justify-content: space-around;*/
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    /*padding: 0 16px;*/
    -ms-flex-item-align: center;
        align-self: center;
    width: 100%;
}
#resumeEditor {
  min-width: 30%;
  background: #444;
}
#resumePreview {
  background: #777;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
svg.icon {
  height: 1em;
  width: 1em;
  fill: currentColor;
  vertical-align: -0.1em;
  font-size: 16px;
}



